</template>
<script>
export default {
  methods: {
    socialLink: () => {
      window.open('https://www.linkedin.com/in/valentin-messiaen-2aa650a9/', '_blank');
    },
    message: () => {
      window.location.href = 'mailto:valentin.messiaen@gmail.com?subject=Votre sujet&body=Que puis-je faire pour vous ?';
    }
  }
};
</script>
<style>
